@tailwind base;
@tailwind components;
@tailwind utilities;
@layer base {
  @font-face {
    font-family: "LOPF-Sans";
    font-weight: 400;
    src: url(../fonts/Sans-Regular.woff) format("woff"), url(../fonts/Sans-Regular.woff2) format("woff2");
  }
  @font-face {
    font-family: "LOPF-Sans";
    font-weight: 600;
    src: url(../fonts/Sans-SemiBold.woff) format("woff"), url(../fonts/Sans-SemiBold.woff2) format("woff2");
  }
  @font-face {
    font-family: "LOPF-Sans";
    font-weight: 700;
    src: url(../fonts/Sans-Bold.woff) format("woff"), url(../fonts/Sans-Bold.woff2) format("woff2");
  }
  @font-face {
    font-family: "LOPF-Serif";
    font-weight: 700;
    src: url(../fonts/Serif.woff) format("woff"), url(../fonts/Serif.woff2) format("woff2");
  }
}
/*
* Section heading 'shapes'
*/
.pattern::before {
  content: "";
  @apply lg:-left-12 -left-6 -top-8 lg:-top-16 z-20 block lg:-bottom-24 -bottom-12 -right-12 lg:-right-24 z-20 block absolute bg-no-repeat bg-contain bg-center;
  background-image: url(../../public/images/background-1.svg);
}
.bg-purple .pattern::before {
  background-image: url(../../public/images/background-2.svg);
}
.bg-purple .pattern::before--alt {
  background-image: url(../../public/images/background-5.svg);
}
.bg-sky .pattern::before {
  background-image: url(../../public/images/background-3.svg);
}
.bg-orange .pattern::before {
  background-image: url(../../public/images/background-4.svg);
}

.semicircle-orange, .semicircle-pink, .semicircle-blue, .semicircle-sky {
  position: relative;
  --tw-gradient-stops: white, white 50%, var(--tw-gradient-to) 50%,
    var(--tw-gradient-to);
  @apply to-pink-lightest;
}
.semicircle-orange::before, .semicircle-pink::before, .semicircle-blue::before, .semicircle-sky::before {
  content: "";
  display: block;
  height: 2.5em;
  width: 2.5em;
  position: absolute;
  border-radius: 9999px;
  z-index: 10;
  @apply bg-orange-lightest;
}
.semicircle-orange {
  @apply bg-gradient-to-tr;
}
.semicircle-orange::before {
  @apply top-4 right-4;
}
.semicircle-pink {
  @apply bg-gradient-to-bl;
}
.semicircle-pink::before {
  @apply bottom-4 left-4;
}
.semicircle-sky {
  @apply bg-gradient-to-tl;
}
.semicircle-sky::before {
  @apply top-4 left-4;
}
.semicircle-blue {
  @apply bg-gradient-to-br;
}
.semicircle-blue::before {
  @apply bottom-4 right-4;
}

/*
* Clipping paths
*/
.clip-teardrop {
  clip-path: url(#teardrop);
}

/*
* Floating circle shapes
*/
.circle-orange::before, .circle-pink::before, .circle-blue::before, .circle-sky::before {
  content: "";
  display: block;
  height: 5em;
  width: 5em;
  position: absolute;
  opacity: 0.35;
  border-radius: 9999px;
  z-index: 10;
}
.circle-orange::before {
  @apply bg-orange top-0 left-0 transform -translate-y-1/2;
}
.circle-pink::before {
  @apply bg-pink top-0 right-0 transform -translate-y-1/2 translate-x-1/2;
}
.circle-blue::before {
  @apply bg-blue bottom-0 left-0;
}
.circle-sky::before {
  @apply bg-sky top-0 right-0 transform -translate-y-1/2 translate-x-1/2;
}

.overlay {
  position: relative;
}
.overlay::after {
  content: "";
  pointer-events: none;
  height: 130%;
  width: 135%;
  position: absolute;
  right: 0;
  top: -15%;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.bg-signup {
  position: relative;
}
.bg-signup::before {
  content: "";
  @apply bg-pink rounded-full block w-24 h-24 absolute left-0 top-0;
}
.bg-signup::after {
  --tw-gradient-stops: white, white 50%, var(--tw-gradient-to) 50%,
    var(--tw-gradient-to);
  content: "";
  @apply rounded-full block w-48 h-48 absolute bottom-0 right-0  to-sky bg-gradient-to-br;
}

.wp-block-person {
  @apply border-4 border-sky;
}
.wp-block-person + .wp-block-person {
  margin-top: 1.5em;
}
.wp-block-person .wp-block-columns {
  @apply bg-black flex items-center;
}
.wp-block-person [data-type="mrn/button"] {
  position: static;
}
.wp-block-person .wp-block-column {
  position: relative;
}
.wp-block-person .block-editor-block-list__block {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}
.wp-block-person .button {
  position: absolute;
  transform-origin: center;
  @apply right-4 top-1/2 transition;
  transform: translateY(-50%);
}
.wp-block-person .button path {
  stroke-width: 100;
}
.wp-block-person .button[aria-expanded=true] {
  transform: translateY(-50%) rotate(90deg);
}
.wp-block-person h3 {
  @apply text-4xl font-bold text-white mt-2 mb-1  !important;
}
.wp-block-person h4 {
  @apply text-2xl font-bold text-blue  !important;
}
.wp-block-person .person-details {
  @apply py-4 lg:px-12;
}
.wp-block-person figure {
  @apply my-0 !important;
}
.wp-block-person figure figcaption {
  display: none;
}

#app {
  overflow: hidden;
  position: relative;
}

.has-background {
  @apply p-4 md:p-8 !important;
}
.has-background h2,
.has-background h3,
.has-background h4,
.has-background h5,
.has-background h6 {
  margin-top: 0 !important;
}

.hidden-on-mobile {
  @apply hidden md:block;
}

.single-project #ez-toc-container .ez-toc-title {
  display: none;
}
#ez-toc-container .ez-toc-title {
  @apply text-2xl;
}
#ez-toc-container li {
  @apply mb-4 text-gray-600;
}

.ez-toc-title {
  @apply text-xl font-bold mb-4;
}

.screen-reader-text {
  display: none;
}

.nav-links {
  @apply mt-8 flex justify-between;
}

.nav-previous,
.nav-next {
  @apply inline-flex px-6 py-2 text-lg text-white bg-blue border-0 rounded focus:outline-none hover:bg-blue-light;
}

.is-style-large {
  @apply text-2xl font-extrabold;
}

.alignfull {
  width: 100% !important;
  max-width: none !important;
}

hr,
.wp-block-separator,
.prose hr {
  width: 40%;
  opacity: 1;
  @apply border-t-0 border-b-2 border-sky;
}
hr.is-style-wide,
.wp-block-separator.is-style-wide,
.prose hr.is-style-wide {
  width: 100%;
}

.prose hr {
  margin-left: 0 !important;
}

.prose ul > li::before {
  @apply bg-blue-lightest !important;
  transform: scale(2.5);
  clip-path: url(#teardrop);
  border-radius: 0;
}

.wp-block-columns {
  @apply flex gap-4;
}

.wp-block-column {
  @apply flex-1;
}

.wp-block .wp-block-media-text {
  display: grid;
  grid-template-columns: 50% 1fr;
}
.wp-block .wp-block-media-text .wp-block-media-text__content {
  padding: 0 8% 0 8%;
}

.wp-block-buttons {
  @apply mb-4;
}
.wp-block-buttons .wp-block-button__link {
  @apply inline-block text-blue text-lg rounded-full bg-sky no-underline py-3 px-8 border-blue-light hover:border-blue border;
}

details {
  @apply mb-4 p-4 border border-blue;
}
details > div {
  @apply pt-6;
}

summary {
  @apply bg-sky py-3 px-4 -m-4 text-blue cursor-pointer text-lg;
}

cite p::before,
cite p::after {
  content: none !important;
}